import React, { Children, HTMLAttributes } from 'react';
import cx from 'classnames';

import { Container } from '../Container';

import styles from './ImageSection.module.scss';

export interface ImageSectionProps
    extends Omit<HTMLAttributes<HTMLElement>, 'className'> {
    children: React.ReactNode;
    direction?: 'row' | 'row-reverse';
    layout?: 'half' | 'imageTwoThirds' | 'contentTwoThirds';
}

// Define placeholder components for ImgColumn and ContentColumn
const ImgColumn: React.FC<{ children: React.ReactNode }> = ({ children }) => (
    <>{children}</>
);
const ContentColumn: React.FC<{ children: React.ReactNode }> = ({
    children,
}) => <>{children}</>;

interface ImageSectionComponent extends React.FC<ImageSectionProps> {
    ImgColumn: typeof ImgColumn;
    ContentColumn: typeof ContentColumn;
}

const ImageSection: ImageSectionComponent = ({
    children,
    direction = 'row',
    layout = 'half',
    ...rest
}) => {
    // Identify and render columns with conditional class for ContentColumn based on direction
    const imgColumnContent = Children.toArray(children).find(
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        (child) => (child as any).type === ImgColumn
    );
    const contentColumnContent = Children.toArray(children).find(
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        (child) => (child as any).type === ContentColumn
    );

    // Remove unnecessary props
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { className, ...sectionProps } = rest as HTMLAttributes<HTMLElement>;

    return (
        <section className={styles.base} {...sectionProps}>
            <Container>
                <div
                    className={cx(
                        styles['two-column-section'],
                        styles[`two-column-section--${direction}`],
                        styles[`two-column-section--${layout}`]
                    )}
                >
                    <div className={cx(styles['content-column'])}>
                        {contentColumnContent}
                    </div>
                    <div className={cx(styles['image-column'])}>
                        {imgColumnContent}
                    </div>
                </div>
            </Container>
        </section>
    );
};

// Assign static properties
ImageSection.ImgColumn = ImgColumn;
ImageSection.ContentColumn = ContentColumn;

export default ImageSection;
